







import Vue from 'vue'
import { Dialog } from 'vant'

export default Vue.extend({
  name: 'setting',
  methods: {
    cleanCache () {},
    onCleanCacheClick () {
      Dialog.confirm({
        title: '提示',
        message: '确定清除缓存吗?'
      }).then(this.cleanCache).catch(() => {})
    }
  }
})
